// Mixin
@mixin placeholder {
    &::-webkit-input-placeholder {
        @content
            }
    &:-moz-placeholder {
        @content
            }
    &::-moz-placeholder {
        @content
            }
    &:-ms-input-placeholder {
        @content
            }
}

@mixin inline-percent($percentage,$align,$vertical-align) {
    display: inline-block;
    width: $percentage;
    text-align: $align;
    vertical-align: $vertical-align;
}
@mixin button($bkg-color,$typo-color,$border-color) {
    background-color: $bkg-color; //    border: 1px solid darken( $color-primary, 20%);
    color: $typo-color;
    border: 3px solid $border-color;
    border-radius: 8px;
    padding: 6px 4px;
    text-align: center;
    display: block;
    text-transform: capitalize;
    text-decoration: none;
    font-size: 1.1em;
    font-weight: 700;
    transition: all 0.7s ease;
    cursor: pointer;
    &:hover,
    &:active {
        background-color: darken( $bkg-color, 20%);
    }
}
// Estilos input
//colorear input
