// CIERRE MENU PRINCIPAL **********************************************************************
//**************************************************************************************
.block {
    height: 189px;
    transition: all 0.7s ease;
    box-shadow: 0 0 0 rgba(0, 0, 0, 0.3);
    cursor: pointer;

    &:hover {
        transform: translateY(-15%);
        box-shadow: 0 19px 19px rgba(0, 0, 0, 0.3);
    }

    @include media(xss) {
        height: 200px;
    }

    @include media(xs) {
        height: 200px;
    }
}

.block-gray {
    @extend .block;
    background-color: $bkg-color-box-a;
}

.block-white {
    @extend .block;
    background-color: $bkg-color-box-b;
}

.border-top {
    border-top: 3px solid $color-primary;
}

//grid
$numero-columnas: 6;
$grilla: 768px;

[class*='col-'] {
    float: left;
    padding: 0.8em;
    width: 100%;
}

@mixin grilla {
    @media only screen and(min-width: #{$grilla}) {
        @content;
    }
}

@include grilla {
    @for $i from 1 through $numero-columnas {
        .col-#{$i} {
            width: 100% / $numero-columnas * $i;
        }
    }
}

